.logo{
    width:120px;
}
.btn_call{
    border-radius:25px;
    padding: 6px 20px;
    margin-left: 20px;
    color: #fff;
}
.nav-item {
    margin: 0 10px;
    position: relative;
}
.nav-item:after{
    content:"";
    display:block;
    position: absolute;
    bottom:-25px;
    left: 0%;
    right: 0;
    width: 100%;
    height: 2px;
    background-color: #5757ff;
    border-radius:3px;
    transform:scale(0);
}
.nav-item:hover:after{
    transform:scale(1);
    transition: transform .3s;
}
.number{
    font-size: 1rem;
}
.header{
    box-shadow: 1px 1px 3px 1px silver;

}
.navbar{
    padding: 0;
}
.navbar-nav{
    flex-direction: row;
}
.navbar-content{
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.navbar-nav{
    margin-right: auto;
}

@media screen and (max-width:992px){
    .navbar{
        justify-content: center;
    }
    .navbar-content{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .navbar-nav{
        margin-right: 0;
        margin-bottom: 20px;
    }
    .nav-item:after{
        bottom: 0;
    }
}